.portfolio__container {
  grid-template-columns: repeat(3, 270px);
  justify-content: center;
  column-gap: 1.8rem;
}

.portfolio__content {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--container-color);
  padding: 6rem 2.5rem 2rem 2.5rem;
}

.portfolio__icon {
  display: block;
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-1);
}

.portfolio__title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-1);
  font-weight: var(--font-medium);
}

.portfolio__button {
  color: var(--title-color);
  font-size: var(--small-font-size);
  display: inline-flex;
  align-items: center;
  column-gap: 0.25rem;
  cursor: pointer;
}

.portfolio__button-icon {
  font-size: 1rem;
  transition: 0.3s;
}

.portfolio__button:hover .portfolio__button-icon {
  transform: translateX(0.25rem);
}

.portfolio__modal-link {
  text-align: center;
  margin-bottom: var(--mb-2);
}

.portfolio__modal-link a {
  text-decoration: none;
  color: var(--title-color);
  transition: 0.3s;
}

.portfolio__modal-link a:hover {
  color: #e6c85d;
}

.portfolio__modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: var(--z-modal);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}

.portfolio__modal-content {
  width: 500px;
  position: relative;
  background-color: var(--container-color);
  padding: 4.5rem 2.5rem 2.5rem;
  border-radius: 1.5rem;
}

.portfolio__modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 1.5rem;
  color: var(--title-color);
  cursor: pointer;
}

.portfolio__modal-title,
.portfolio__modal-description {
  text-align: center;
}

.portfolio__modal-title {
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1);
}

.portfolio__modal-description {
  font-size: var(--small-font-size);
  padding: 0 2.5rem;
  margin-bottom: var(--mb-2);
}

.portfolio__modal-services {
  row-gap: 0.75rem;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .portfolio__container {
    grid-template-columns: repeat(3, 218px);
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .portfolio__container {
    grid-template-columns: repeat(2, 2fr);
  }

  .portfolio__content {
    padding: 3.5rem 0.5rem 1.25rem 1.5rem;
  }

  .portfolio__icon {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 576px) {
  .portfolio__container {
    grid-template-columns: repeat(1, 2fr);
  }

  .portfolio__modal-content {
    padding: 4.5rem 1.5rem 2.5rem;
  }

  .portfolio__modal-description {
    padding: 0;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .portfolio__container {
    grid-template-columns: max-content;
  }

  .portfolio__content {
    padding-right: 6rem;
  }
}

/* LOGO */
.portfolio__logo {
  max-width: 100%;
  height: 4rem;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  object-fit: contain;
  object-position: left;
  margin-bottom: 1rem;
}

/* Pagination Buttons */
.portfolio__pagination__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
}
.portfolio__pagination__buttons > button {
  background-color: var(--container-color);
  border: 2px solid var(--title-color);
  padding: 0.5rem 1rem;
  font-size: var(--small-font-size);
  color: var(--title-color);
  cursor: pointer;
  transition: 0.3s;
  border-radius: 0.7rem;
}

.portfolio__pagination__buttons > button:hover {
  background-color: var(--title-color);
  color: var(--container-color);
}

.portfolio__pagination__buttons > button:disabled {
  background-color: var(--title-color);
  color: var(--container-color);
  cursor: not-allowed;
  opacity: 0.7;
}
